.row{
    display: flex;
    flex-direction: row;
}

.col{
    display: flex;
    flex-direction: column;
    margin: 0px 10px;
}

.btnCol{

}

.taskCol{

}

.ticketCol{

}

.container{
    min-height: 80vh;
    width: 80vw;
    margin-top: 10vh;
    margin-left: 10vw;
    padding-bottom: 2vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.btnTool, .greenBtn{
    color: black;
    margin: 10px 5px;
    font-size: 18px;
    padding: 5px 5px;
    outline: none;
    background-color: transparent;
    border: 2px #14a712 solid;
    border-radius: 5px;
    transition: all linear .3s;
    cursor: pointer;
}

.greenBtn:hover{
    color: white;
    background-color: #14a712;
}

.btnTool{
    border: 2px #1471c2 solid;
    width: 100px;
}

.btnTool:hover{
    color: white;
    background-color: #1471c2;
}

.komax{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    margin: 0px 0px;
}

@media (max-height: 420px) {
    .TaskDetailPage{
        margin-top: 10vh;
    }
}

@media (max-width: 420px) {
    .container{
        width: 85vw;
        flex-direction: column;
    }

    .btnTool, .greenBtn{
        font-size: 16px;
    }
}